<template>
  <div class="secure">
    <van-nav-bar
      left-text="返回"
      title="安全中心"
      left-arrow
      @click-left="back"
    />

    <div class="content">
      <van-cell is-link @click="showModifyPasswordPopup">修改密码</van-cell>
      <van-cell is-link @click="showDestroyDialog" >注销账号</van-cell>
    </div>

    <van-popup v-model="show" closeable round position="bottom" :style="{ height: '60%' }">
      <div class="form-box">
        <van-form>
          <div class="title">修改密码</div>
          <van-field
            v-model="oldPassword"
            label="旧密码"
            placeholder="6-12位由数字和字母组成的字符"
            :type="isOldPassword ? 'password' : 'text'"
            :right-icon="isOldPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="viewOldPassword"
          />
          <van-field
            v-model="newPassword"
            label="新密码"
            placeholder="6-12位由数字和字母组成的字符"
            :type="isNewPassword ? 'password' : 'text'"
            :right-icon="isNewPassword ? 'closed-eye' : 'eye-o'"
            @click-right-icon="viewNewPassword"
          />
          <div class="btns-box">
            <van-button class="btn" block native-type="submit" @click='updatePassword'>
              确&nbsp;&nbsp;认
            </van-button>
          </div>

        </van-form>
      </div>
    </van-popup>
  </div>

  
</template>

<script>
  import {validForm} from '../assets/js/validForm'
  import {utils} from '../assets/js/utils'
  import '../assets/less/secure.less'
  export default {
    data(){
      return{
        show : false,
        oldPassword: '',
        newPassword: '',
        isOldPassword:true,
        isNewPassword:true,
      }
    },
    methods:{
      back(){
        this.$router.go(-1);
      },
      // 显示密码
      viewOldPassword(){
        this.isOldPassword = !this.isOldPassword;
      },
      viewNewPassword(){
        this.isNewPassword = !this.isNewPassword;
      },
      showModifyPasswordPopup(){
        this.show = true;
      },
      // 更新密码
      updatePassword(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }

        // 验证表单
        let o = {
          oldPassword: {
            value: this.oldPassword,
            // 6-12位由数字和字母组成的字符
            reg: /^(?![0-9]+$)(?![a-zA-Z]+$)[a-zA-z0-9]{6,12}$/,
            errorMsg: '旧密码格式不正确'
          },
          newPassword: {
            value: this.newPassword,
            // 6-12位由数字和字母组成的字符
            reg: /^(?![0-9]+$)(?![a-zA-Z]+$)[a-zA-z0-9]{6,12}$/,
            errorMsg: '新密码格式不正确'
          }
        }

        if(!validForm.valid(o)){
          // 验证不通过
          // console.log("验证不通过")
          return;
        }

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
          loadingType: "spinner",
        });

        let data = utils.queryString({
          appkey: this.appkey,
          tokenString: tk,
          password: this.newPassword,
          oldPassword: this.oldPassword
        })

        this.axios({
          method: 'POST',
          url:'/updatePassword',
          data
        }).then(result => {
          this.$toast.clear();
            // this.$toast(result.data.msg)
          if(result.data.code == 'E001'){
            this.$toast.success(result.data.msg)
            this.show = false;
            setTimeout(() => {
              this.$router.push({name: 'Login'})
            }, 1000);
          }else{
            this.$toast.fail(result.data.msg)
          }
          // console.log("updatePassword result ==> ",result)
        }).catch(err =>{
          this.$toast.clear()
          // console.log("updatePassword result ==> ",err)
        })   
      },
      // 注销账号
      destroyAccount(){
        let tk = localStorage.getItem('CSTK');
        // 不存在token
        if(!tk){
          return;
        }

        this.$toast.loading({
          message: "加载中...",
          forbidClick: true,
          duration: 0,
          loadingType: "spinner",
        });

        let data = utils.queryString({
          appkey: this.appkey,
          tokenString: tk,
        })

        this.axios({
          method: 'POST',
          url:'/destroyAccount',
          data
        }).then(result => {
          this.$toast.clear();
          this.$toast(result.data.msg)
            // this.$toast(result.data.msg)
          if(result.data.code == 'G001'){
            localStorage.removeItem("CSTK")
            // this.$toast.success(result.data.msg)
            setTimeout(() => {
              this.$router.push({name: 'Login'})
            }, 1000);
          }else{
            // this.$toast.fail(result.data.msg)
          }
          // console.log("destroyAccount result ==> ",result)
        }).catch(err =>{
          this.$toast.clear()
          // console.log("destroyAccount result ==> ",err)
        })   

      },

      // 注销 弹出框
      showDestroyDialog(){
        this.$dialog.confirm({
          message: '确定要注销账号吗？一旦注销无法恢复',
        })
          .then(() => {
            this.destroyAccount();
          })
          .catch(() => {
            // on cancel
          });
      }

    }
  }
</script>

